.root {
  position: relative;
  display: flex;
  justify-content: center;
  background: url(../../public/background.png), lightgray 50% / cover no-repeat;
}

.wifi-icon {
  position: absolute;
  flex-shrink: 0;
  fill: var(--ink-06, #fff);
  left: 108px;
  top: 194px;
}

.heading {
  position: absolute;
  left: 159px;
  top: 192px;
  width: 144px;
  height: 68px;
  flex-shrink: 0;
  color: #fff;
  text-align: center;
  font-feature-settings: "clig" off, "liga" off;
  font-family: Inter;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.active-box {
  position: absolute;
  display: flex;
  justify-content: center;
  top: 281px;
  width: 367px;
  height: 452px;
  flex-shrink: 0;
  background-color: white;
  border-radius: 42px;
}

.reset {
  position: absolute;

  top: 38px;
  color: #000;
  font-family: Inter;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

input {
  width: 244px;
  height: 52px;
  flex-shrink: 0;
  border: 1px solid bla;
  border-radius: 10px;

  color: black;
}
.input-email {
  position: absolute;

  top: 119px;
}

input[type="email"]::placeholder {
  text-align: center;
  color: black;
}

.btn-reset {
  position: absolute;
  top: 220px;
  display: flex;
  width: 194px;
  height: 47px;
  padding: 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
  border: none;
  border-radius: 4px;
  background: var(--utility-warning, #ffac4b);
}

.login {
  position: absolute;
  left: 230px;
  top: 300px;
  color: #1b1a1a;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 520;
  line-height: normal;
  text-decoration-line: underline;
  width: 100px;
  height: 180px;
}
.error {
  position: absolute;
  right: 61px;
  top: 175px;
  color: #fc1111;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.error1 {
  position: absolute;
  right: 61px;
  top: 278px;
  color: #fc1111;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
