.root {
  position: relative;
  display: flex;
  justify-content: center;
  background: url(../../public/background.png), lightgray 50% / cover no-repeat;
}

.wifi-icon {
  position: absolute;
  width: 43px;
  height: 36px;
  flex-shrink: 0;
  fill: var(--ink-06, #fff);
  left: 130px;
  top: 169px;
}

.heading {
  position: absolute;
  left: 180px;
  top: 170px;
  width: 142px;
  height: 36px;
  flex-shrink: 0;
  color: #fff;
  text-align: center;
  font-feature-settings: "clig" off, "liga" off;
  font-family: Inter;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.active-box {
  position: absolute;
  display: flex;
  justify-content: center;
  top: 240px;
  width: 370px;
  height: 505px;
  flex-shrink: 0;
  background-color: white;
  border-radius: 42px;
}

.signup-heading {
  position: absolute;
  top: 18px;
  width: 130px;
  height: 38px;
  flex-shrink: 0;
  color: #000;
  font-family: Inter;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

input {
  width: 268px;
  height: 50px;
  border: 1px solid bla;
  border-radius: 10px;
  color: black;
}
.input-number {
  position: absolute;

  top: 93px;
}

input[type="number"]::placeholder {
  text-align: center;
  color: black;
}

.input-password {
  position: absolute;

  top: 178px;
}
input[type="password"]::placeholder {
  text-align: center;
  color: black;
}
.input-confirm-password {
  position: absolute;

  top: 271px;
}

.btn-login {
  position: absolute;
  top: 372px;
  display: flex;
  width: 194px;
  height: 47px;
  padding: 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
  border: none;
  border-radius: 4px;
  background: var(--utility-warning, #ffac4b);
}

.info-txt {
  position: absolute;
  left: 49px;
  top: 462px;
  color: #100f0f;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.signup {
  position: absolute;
  left: 250px;
  top: 462px;
  color: #1b1a1a;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 520;
  line-height: normal;
  text-decoration-line: underline;
  width: 83px;
  height: 20px;
  flex-shrink: 0;
}
.error {
  position: absolute;
  right: 61px;
  top: 156px;
  color: #fc1111;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.error1 {
  position: absolute;
  right: 61px;
  top: 238px;
  color: #fc1111;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.error2 {
  position: absolute;
